import { InteractivityChecker } from '@angular/cdk/a11y';
import {
  ChangeDetectionStrategy,
  Component,
  ComponentFactoryResolver,
  ComponentRef,
  Input,
  OnInit,
  Type,
  ViewContainerRef
} from '@angular/core';
import {
  OnSofFocus,
  SOF_FOCUS_COMPONENT
} from '@sofico-framework/ui-kit/directives/focus';
import { ListItemComponent } from '@sofico-framework/ui-kit/types';

@Component({
  selector: 'sof-overview-list-item',
  changeDetection: ChangeDetectionStrategy.OnPush,
  template: ``,
  styleUrls: ['./overview-list-item.component.scss'],
  providers: [
    { provide: SOF_FOCUS_COMPONENT, useExisting: OverviewListItemComponent }
  ]
})
export class OverviewListItemComponent<T> implements OnInit, OnSofFocus {
  private componentRef: ComponentRef<ListItemComponent<T>>;
  private localTc: string;
  private localEntity: T;

  /**
   * The translation context.
   */
  @Input() set tc(tc: string) {
    this.localTc = tc;

    if (this.componentRef) {
      (this.componentRef.instance as any).tc = this.localTc;
    }
  }

  /**
   * The entity we want to set.
   */
  @Input() set entity(entity: T) {
    this.localEntity = entity;

    if (this.componentRef) {
      (this.componentRef.instance as any).entity = this.localEntity;
    }
  }

  /**
   * The component that must be dynamically injected into the template.
   */
  @Input() dynamicRowComponent: Type<ListItemComponent<T>>;

  constructor(
    private componentFactoryResolver: ComponentFactoryResolver,
    private viewContainerRef: ViewContainerRef,
    private interactivityChecker: InteractivityChecker
  ) {}

  ngOnInit(): void {
    const factory = this.componentFactoryResolver.resolveComponentFactory(
      this.dynamicRowComponent
    );
    this.componentRef = this.viewContainerRef.createComponent(factory);
    (this.componentRef.instance as any).tc = this.localTc;
    (this.componentRef.instance as any).entity = this.localEntity;
  }

  sofFocus(): void {
    if (
      this.componentRef.instance &&
      typeof ((this.componentRef.instance as any) as OnSofFocus).sofFocus ===
        'function'
    ) {
      ((this.componentRef.instance as any) as OnSofFocus).sofFocus();
    } else if (
      this.interactivityChecker.isFocusable(
        this.componentRef.location.nativeElement
      )
    ) {
      this.componentRef.location.nativeElement.focus();
    } else if (
      this.interactivityChecker.isFocusable(
        this.componentRef.location.nativeElement?.firstChild
      )
    ) {
      this.componentRef.location.nativeElement.firstChild.focus();
    } else {
      throw Error(
        'The dynamic row component nor its first child is focusable.'
      );
    }
  }
}
